<template>
    <div class="fileload">

        <div class="contain" v-if="flag">
           <!--  <el-upload class="upload-demo" drag action="http://localhost:8089/music/loadmusic" multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload> -->

            <div class="title">

                <h1>欢迎添加音乐 ^_^!</h1>
            </div>
            <div class="address">
                <span>选择文件:</span>
                <input type="file" placeholder="" ref="fileadd" @change="getfile" id="filePath">
                <div class="mask" @click="middlefile">{{ filename }}</div>
            </div>
            <div class="comit">
                <button @click="comitmusic">确认</button>
            </div>
        </div>
        <el-button type="primary" @click="addmusic" class="opreatmusic">添加音乐</el-button>
        <el-button type="primary" @click="closebar" class="opreatmusic">关闭</el-button>
<!--         <div class="example">
            <img :src="imgurl" alt="" style="width:100px;height:100px">
            <audio :src="musicurl" controls></audio>
        </div> -->
    </div>
</template>
<script>

import { getmusicinfo, getDuration } from 'assets/js/musicinfo.js'
import { formmusic, formmusicfile } from '@/network/mepage.js'

export default {
    name: 'fileload',
    data() {
        return {
            flag: false,
            defaultname: "点击上传文件",
            filename: '',
            currentfile: {},
            musicduration: '',
            musicfile: {
                music: '',
                singer: '',
                space: '',
                time: '',
                url: '',
                img: ''
            },
            targetfile: {},
            fileform: '',
            imgurl: '',
            musicurl: ''
        }
    },
    created() {
        this.filename = this.defaultname

    },
    methods: {
        addmusic() {
            this.flag = true
            this.$emit('filescroll')
        },
        closebar() {
            this.flag = false

        },
        middlefile() {

            this.$refs.fileadd.click()
        },
        getfile(e) {
            this.currentfile = this.$refs.fileadd.files[0]


            switch (this.currentfile.type) {
                case 'image/jpeg': this.imgurl = URL.createObjectURL(this.currentfile)
                    break;
                case 'audio/mpeg': this.musicurl = URL.createObjectURL(this.currentfile)
                    break;
            }
            getmusicinfo(this.currentfile).then(res => {
                this.musicfile.music = res.title
                this.musicfile.singer = res.artist
                this.filename = this.musicfile.music

                var base64String = "";
                for (var i = 0; i < res.picture.data.length; i++) {
                    base64String += String.fromCharCode(res.picture.data[i]);
                }
                this.musicfile.img = "data:" + res.picture.format + ";base64," + window.btoa(base64String);


            })
            getDuration(e.currentTarget, this.currentfile).then(res => {
                this.musicfile.time = res.duration
            })
        },

        getObjectURL(file) {
            var url = null;
            if (window.createObjcectURL != undefined) {
                url = window.createOjcectURL(file);
            } else if (window.URL != undefined) {
                url = window.URL.createObjectURL(file);
            } else if (window.webkitURL != undefined) {
                url = window.webkitURL.createObjectURL(file);
            }
            return url;
        },


        comitmusic() {
            let musicform = {}
            this.musicfile.space = Math.round(this.currentfile.size / 10000) / 100 + 'MB'

            this.musicfile.url = this.currentfile.name
            musicform.musicinfo = this.musicfile
            musicform.file = this.currentfile
            let formData = new FormData()
            formData.append('file', this.currentfile)

            formmusicfile(formData).then(res => {
                if (res.data.state == 200) {
                    formData.append('info',JSON.stringify(musicform) )
                    this.$toast.show('文件下载成功', 2000)
                    formmusic(formData).then(res => {
                        if (res.data.state == 200) {
                            this.$toast.show('添加成功', 2000)
                        } else {
                            this.$toast.show(res.data.message)
                        }
                        console.log(res)
                    })

                } else {

                    this.$toast.show(res.data.message)
                }
                console.log(res)
            })


        }
    }
}
</script>
<style scoped lang="less">
@media screen and (min-width : 992px) {
    .fileload {
        width: 90%;
        height: 100%;
        /*         float: left; */
        //background-color: rgba(100, 100, 100, .4);
        z-index: 99;
        margin: 50px auto;

        .contain {
            width: 800px;
            height: 500px;
            margin: 100px auto;
            display: flex;
            justify-content: space-evenly;
            flex-direction: column;
            border: 2px solid black;
            border-radius: 4px;
            transition: all .3s;
            padding: 10px;
            background-color: rgba(0, 0, 0, .3);

            div {
                text-align: left;
                color: aqua;
                font-size: 25px;
                font-family: "宋体";
                margin-left: 20%;
            }

            .title {}

            .address {
                position: relative;

                input {
                    opacity: 0;
                    /*兼容ie*/

                    /* 增大不同浏览器的可点击区域 */
                    cursor: pointer;
                }

                .mask {
                    position: absolute;
                    padding: 2px;
                    height: 25px;
                    font-size: 20px;
                    line-height: 25px;
                    top: 5px;
                    left: 0px;
                    width: 300px;
                    text-align: center;
                    background-color: #fff;
                    color: black;
                    border-radius: 5px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }

            }

            .comit {
                margin-left: 30%;

                button {
                    width: 100px;
                    border-radius: 4px;
                    background-color: orange;
                }
            }

            .opreatmusic {
                width: 100px;
                text-align: center;
                font-size: 20px;
                border-radius: 4px;
                background-color: greenyellow;
                margin: 10px;
            }
        }
    }
}

@media screen and (max-width:767px) {
    .fileload {
        width: 80%;
        height: 100%;
        /* float: left; */
        //    background-color: rgba(100, 100, 100, .4);
        z-index: 99;
        text-align: center;
        margin: 100px auto;

        .contain {
            width: 80%;
            height: 300px;
            margin: 100px auto;
            display: flex;
            justify-content: space-evenly;
            flex-direction: column;
            border: 2px solid black;
            border-radius: 4px;
            transition: all .3s;
            padding: 10px;
            background-color: rgba(0, 0, 0, .3);

            div {
                text-align: left;
                color: aqua;
                font-size: 12px;
                font-family: "宋体";
                margin-left: 20%;
            }

            .title {}

            .address {
                position: relative;

                input {
                    opacity: 0;
                    /*兼容ie*/

                    /* 增大不同浏览器的可点击区域 */
                    cursor: pointer;
                }

                .mask {
                    position: absolute;
                    padding: 2px;
                    height: 25px;
                    font-size: 15px;
                    line-height: 20px;
                    top: 20px;
                    left: -35px;
                    width: 150px;
                    text-align: center;
                    background-color: #fff;
                    color: black;
                    border-radius: 5px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }

            }

            .comit {
                margin-left: 30%;

                button {
                    width: 100px;
                    border-radius: 4px;
                    background-color: orange;
                }
            }

            .opreatmusic {
                width: 100px;
                text-align: center;
                font-size: 20px;
                border-radius: 4px;
                background-color: greenyellow;
                margin: 10px;
            }
        }
    }
}
</style>


