import jsmediatags from 'jsmediatags'


export function getmusicinfo(file) {
    return new Promise((resolve, reject) => {
        jsmediatags.read(file, {
            onSuccess: function (tag) {
                resolve(tag.tags)
            },
            onError: function (error) {
                console.log(":(" + error.type + error.info)
            }
        })
    }) 
}
// 获取时长
export function getDuration(target, file) {
    return new Promise((resolve, reject)=>{
        let reader = new FileReader();
        let that = this
        if (target.files && file) {
            let reader = new FileReader();
            let audio = document.createElement('audio');
            reader.onload = function (e) {
                audio.src = e.target.result;
                audio.addEventListener('loadedmetadata', function () {
                    let duration = typeof (audio.duration) == "number" ? audio.duration : 0
                    // 获取到音乐时长
                    var timeF = Math.floor(duration / 60)
                    var timeS = Math.floor(duration - timeF * 60)
                    var musicduration = timeF + ':' + timeS
                    var time={
                        steps:duration,
                        duration:musicduration
                    }
                    resolve(time)
                }, false);
    
            };
            reader.readAsDataURL(file);
        }
    })
    
 }


