<template>
    <div class="datacharts">
        <div id="chart1">
            hello
        </div>  
    </div>
</template>
<script>
export default {
    name: 'DataCharts',
    mounted() {
        this.$nextTick(() => {
            //console.log(document.getElementById('chart1'))
            var myChart = this.echarts.init(document.getElementById('chart1'));
            // 绘制图表
            var option = {
                tooltip: {
                    trigger: 'item'
                },
                legend: {
                    top: '5%',
                    left: 'center'
                },
                series: [
                    {
                        name: 'Access From',
                        type: 'pie',
                        radius: ['40%', '70%'],
                        avoidLabelOverlap: false,
                        itemStyle: {
                            borderRadius: 10,
                            borderColor: '#fff',
                            borderWidth: 2
                        },
                        label: {
                            show: false,
                            position: 'center'
                        },
                        emphasis: {
                            label: {
                                show: true,
                                fontSize: '40',
                                fontWeight: 'bold'
                            }
                        },
                        labelLine: {
                            show: false
                        },
                        data: [
                            { value: 1048, name: '科学' },
                            { value: 735, name: '情感' },
                            { value: 580, name: '社会' },
                            { value: 484, name: '编程' },
                            { value: 300, name: '学习' }
                        ]
                    }
                ]
            }
            option && myChart.setOption(option);
        })

    }
}
</script>
<style lang="less" scoped>
@media screen and (min-width : 992px) {
    .datacharts {
        #chart1 {
            width: 800px;
            height: 400px;
            margin: 50px auto;
            border-radius: 4px;
            background-color: rgba(100, 100, 100, .4);
        }
    }

}

@media screen and (max-width : 992px) {
    .datacharts {
        #chart1 {
            width: 300px;
            height: 400px;
            margin: 50px auto;
            border-radius: 4px;
            background-color: rgba(100, 100, 100, .4);

        }
    }

}
</style>


