<template>
    <div id="mepage" ref="me">
        <BlogCard ></BlogCard>
        <AppList :personal="applist"></AppList>

        <MuiscPlayer :musiclist="musiclist" v-if="musiclist.length != 0"></MuiscPlayer>
        <FileLoad ref="file" @filescroll="filescroll"></FileLoad>
        <DataChartsVue></DataChartsVue>
    </div>
</template>
<script>
import FileLoad from 'components/common/fileload';

import BlogCard from 'components/content/blogcard';
import AppList from 'components/content/applist';
import MuiscPlayer from 'components/content/musicplayer';

import DataChartsVue from './childpages/DataCharts.vue';

import { getmusic } from '@/network/mepage.js'

export default {
    name: 'mepage',
    data() {
        return {
            applist: [
                {
                    img: require('assets/img/appimg/add.png'),
                    info: '我的'
                },
                {
                    img: require('assets/img/appimg/up.png'),
                    info: '更多'
                }
            ],
            musiclist: [],
            show:false
        }
    },
    created() {
        getmusic().then(res => {
            this.musiclist = res.data
            //console.log(res.data)
        })
    },
    components: {
        BlogCard,
        AppList,
        MuiscPlayer,
        FileLoad,
        DataChartsVue
    },
    methods: {
        filescroll() {
            //如果是组件获取dom元素的话，我们就需要在后面加上一个$el
            this.$nextTick(() => {
                document.documentElement.scrollTo(0, this.$refs.file.$el.offsetTop)
            })
        },

    }
}
</script>
<style scoped>
#mepage {
    background-color: rgba(100, 100, 100, .4);
}
</style>


