<template>
    <div class="musicplayer">
        <div class="header">
            <h2>我的歌曲</h2>
            <img :class="{imgrolate:!player}" :src="currentmusic.img">
        </div>

        <div class="contain">

            <div class="list">

                <Scroll>
                    <ul>
                        <li>歌曲名</li>
                        <li>歌手</li>
                        <li>时长</li>
                    </ul>
                    <ul @click="current(index)" v-for="(item, index) in musiclist" :key="index">
                        <li>{{ item.music }}</li>
                        <li>{{ item.singer }}</li>
                        <li>{{ item.time }}</li>
                    </ul>
                </Scroll>


            </div>
            <div class="info">
                <img v-if="!Object.keys(currentmusic).length == 0" :src="currentmusic.img" alt="">
            </div>
        </div>
        <div class="player">
            <div class="image">
                <img :src="currentmusic.img" alt="">
                <audio ref="player" :src="currentmusic.position" autoplay :currentTime="currentStep">
                    <source />
                </audio>
            </div>
            <div class="controls">
                <div class="el-icon-d-arrow-left" @click="pre"></div>
                <div :class="player ? 'el-icon-video-pause' : 'el-icon-video-play'" @click="middle"></div>
                <div class="el-icon-d-arrow-right" @click="next"></div>
            </div>
            <div class="musicbar">
                <input type="range" min="0" :max="allstep" step="1" v-model="currentStep" @click="changed"
                    :style="{ '--slider': slider }" />
                <span>{{ timeF }}:{{ timeS }}/{{ currentmusic.time }}</span>
            </div>
            <div class="audio">
                <input type="range" min="0" max="1" step="0.01" v-model="volume" />
                <span class="el-icon-headset"></span>
            </div>
        </div>
    </div>
</template>
<script>

import Scroll from 'components/common/scroll/Scroll.vue'

export default {
    name: 'musicplayer',
    props: {
        musiclist: {
            type: Array,
            default() {
                return [{}]
            }
        }
    },
    components: {
        Scroll
    },
    created() {


    },
    mounted() {
        this.timeing()
    },
    data() {
        return {
            currentmusic: {
            },
            player: false,
            currentindex: 0,
            timeS: 0,
            timeF: 0,
            id: -1,
            flag: false,
            currentStep: 0,
            allstep: 0,
            slidernum: 0,
            sliderstep: 0,
            timer: {},
            volume: 0.4
        }
    },
    methods: {
        current(index) {
            this.currentmusic = this.musiclist[index]
            this.player = true
            this.currentindex = index

        },
        middle() {
            this.player = !this.player
            if (this.player) {
                this.$refs.player.play()
            } else {
                this.$refs.player.pause()
            }
        },
        pre() {
            this.player = true
            this.currentindex--
            this.currentindex %= this.musiclist.length
            this.currentmusic = this.musiclist[-this.currentindex]
        },
        next() {
            this.player = true
            this.currentindex++
            this.currentindex %= this.musiclist.length
            this.currentmusic = this.musiclist[this.currentindex]
        },
        timeing() {
            this.$nextTick(() => {
                this.timer = setInterval(() => {
                    if (this.player) {
                        this.timeS++
                        this.currentStep++
                        this.slidernum += this.sliderstep

                    }

                    if (this.timeS == 60) {
                        this.timeF++
                        this.timeS = 0
                    }


                    if (this.id != this.currentindex && Object.keys(this.currentmusic).length != 0) {
                        this.timeF = 0
                        this.timeS = 0
                        this.slidernum = 0
                        this.id = this.currentindex
                        this.currentStep = 0
                        this.allstep = parseInt(this.currentmusic.time) * 60 + parseInt(this.currentmusic.time.slice(2))
                        this.sliderstep = 100 / this.allstep
                    } else if (this.allstep < this.currentStep) {
                        this.next()
                    } else {
                        //console.log('*')
                    }



                }, 1000)

            })

        },
        changed() {
            this.player = false
            this.timeF = Math.floor(this.currentStep / 60)
            this.timeS = (this.currentStep - this.timeF * 60) < 0 ? -(this.currentStep - this.timeF * 60) : (this.currentStep - this.timeF * 60)
            var audio = this.$refs.player;
            audio.play()
            audio.currentTime = this.currentStep;
            this.slidernum = this.currentStep * this.sliderstep
            this.player = true

        }
    },
    computed: {
        slider() {
            return this.slidernum + '%'
        }
    },
    updated() {
        this.$refs.player.volume=this.volume
    }
}
</script>
<style scoped lang="less">
