            }
            .imgrolate{
                
                animation-play-state:paused;
            }

        }

        .contain {
            display: flex;
            height: 100%;
            padding: 5px;

            .list {
                flex: 1;
                text-align: center;
                position: relative;
                height: 600px;

                ul {
                    display: flex;
                    height: 80px;

                    background-color: rgba(0, 0, 0, .4);
                    justify-content: space-around;
                    color: #fff;
                    font-size: 20px;
                    border-bottom: 1px solid pink;

                    &:nth-child(1) {
                        border-radius: 4px 4px 0 0;

                    }

                    &:last-child {
                        border-radius: 0 0 4px 4px;
                    }

                    li {
                        line-height: 80px;

                        &:nth-child(1) {
                            width: 150px;

                        }

                        &:nth-child(2) {
                            flex: 1;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }

                        &:nth-child(3) {
                            width: 150px;

                        }
                    }

                }
            }

            .info {
                flex: 1;
                text-align: center;

                img {
                    width: 300px;
                    height: 300px;
                    margin-top: 100px;
                }
            }
        }

        .player {
            margin-top: 20px;
            padding: 5px;
            width: 100%;
            background-color: rgba(0, 0, 0, .4);
            color: #fff;
            height: 100px;
            display: flex;
            position: absolute;
            bottom: 0px;
            left: 0px;

            .image {
                width: 80px;
                height: 80px;

                img {
                    margin-top: 5px;
                    width: 100%;
                    height: 100%;

                }
            }

            .controls {
                width: 200px;
                height: 80px;
                display: flex;
                margin-top: 15px;
                justify-content: space-around;

                div {
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    line-height: 50px;
                    text-align: center;
                    font-size: 30px;
                    background-color: aqua;


                }

            }

            .musicbar {
                flex: 1;
                height: 80px;

                input {
                    margin-top: 30px;
                    width: 100%;
                    background-size: 75% 100%;
                    -webkit-appearance: none;
                    /*清除系统默认样式*/
                    width: 100%;
                    background-color: linear-gradient(#f44336, #bbb);
                    height: 8px;
                    border-radius: 4px;
                    outline: none;
                    cursor: pointer;
                    background-size: 0% 100%;

                    &::-webkit-slider-thumb {
                        -webkit-appearance: none;
                        width: 20px;
                        height: 20px;
                        border-radius: 50%;
                        background-color: aqua;
                    }

                    &::before {
                        content: " ";
                        display: inline-block;
                        margin-bottom: 8px;
                        border-radius: 4px 0 0 4px;
                        height: 8px;
                        width: var(--slider);
                        background-color: orange;
                    }


                }
            }

            .audio {
                height: 80px;
                width: 100px;
                margin-left: 10px;
                position: relative;
                line-height: 80px;

                input {
                    position: absolute;
                    top: -50px;
                    left: -40px;
                    opacity: 0;
                    margin-bottom: 100px;
                    transform: rotate(-90deg);
                    transition: all .5s linear;

                }

                &:hover input {
                    opacity: 1;
                }

                span {
                    font-size: 20px;
                }
            }
        }
    }
}

